@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,h2,h3,h4,h5,p,a{margin:0}h6{color:#908f8f;margin:0 0 6px 0}svg{width:100%;height:auto;margin:0 0 40px 0}.flex{display:flex;justify-content:space-between}.app{display:flex;flex-direction:column;align-items:center;height:100vh;margin:40px}.app .titleContainer{text-align:center;margin:0 0 40px 0}.app .titleContainer .title{font-weight:500;margin:0 0 10px 0}.app .titleContainer .subtext{color:#677788}.app .inputOutputContainer{width:-webkit-fill-available;width:-moz-available}.app .inputOutputContainer .binInput{width:-webkit-fill-available;width:-moz-available}.app .inputOutputContainer .binInput .binTitle{color:#000;text-align:center}.app .inputOutputContainer .binInput input{font-size:18px;text-align:center;padding:10px 0;width:-webkit-fill-available;width:-moz-available;border-width:2px;border-radius:8px;margin:0 0 40px 0}.app .inputOutputContainer .firstRow,.app .inputOutputContainer .secondRow,.app .inputOutputContainer .thirdRow{background:#f0f0f0;width:-webkit-fill-available;width:-moz-available;padding:8px 8px;border-radius:8px;margin:0 0 10px 0;text-transform:uppercase}@media screen and (min-width: 565px){svg{width:auto}}@media screen and (min-width: 768px){.app{justify-content:center;max-width:968px;margin:0 auto}.app svg{flex:1 1;max-width:500px;margin:0 20px}.app .binContainer{display:flex}.app .inputOutputContainer{flex:1 1;margin:0 20px}.app .inputOutputContainer .binInput{margin:0 0 20px 0}.app .inputOutputContainer .binInput input{margin:0}}
